import {BASE_URL} from './httpClient';

export default class ListServices {
  // [POST] /services/uaa/api/p/customer-logins/email | Get info about email
  static async getListServives(accessToken: string) {
    const endPoint =
      '/services/erequest/api/services/search?sort=displayOrder,asc&page=0&size=10000&sort=createdDate,desc';
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify({
          isAdmin: false,
          status: ['display'],
        }),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getByServiceId(serviceId: string, accessToken: string) {
    const endPoint = `/services/erequest/api/work-flows/get-by-serviceId?serviceId=${serviceId}`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getModeler(modelerId: string, accessToken: string) {
    const endPoint = `/services/idaas-eflow/api/modelers/${modelerId}`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async createTicket(dataBody: any, accessToken: string) {
    const endPoint = `/services/erequest/api/tickets`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(dataBody),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }
}
